







































import Vue from 'vue'
import Page from '@/components/Page.vue'

export default Vue.extend({
  data() {
    return {
      card: '',
      cardError: false,
      email: '1234567',
      wechat: 'WeUI',
    }
  },
  components: { Page },
  methods: {
    save(): void {
      if (this.card.length === 16) {
        this.$toast.success('已完成')
      } else {
        this.cardError = true
        this.$toptips.show('错误提示')
      }
    },
  },
})
